export default {
    data() {
        return {
            swiperOption: {
                loop: true,
                speed: 750,
                slidesPerView: 1,
                spaceBetween: 10,
                autoplay: {
                    delay: 6000
                },
                effect: "fade",
                fadeEffect: {
                    crossFade: true
                },
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
            },

            sliderData: []
        }
    },
    async mounted()
   {
        const slider = await this.$axios.get('/slider_home')
        this.sliderData = slider.data
   }
};
